var CACHE_NAME = "my-site-cache-v1";
var urlsToCache = ["/", "/styles.css", "/index.js", "/fileserver/backend/uploads/abertura.mp4"];

// https://developers.google.com/web/fundamentals/primers/service-workers/?hl=pt-br
self.addEventListener("install", function(event) {
  //sleep(2000000); //2000 segundos
  // Perform install steps
  event.waitUntil(
    caches.open(CACHE_NAME).then(function(cache) {
      console.log("Opened cache");
      //return cache.addAll(urlsToCache);
      return cache.addAll([
        '/',
        '/index.js',
        '/fileserver/backend/uploads/abertura.mp4',
        '/styles.css'
      ]);
    })
  );
});

self.addEventListener("fetch", function(event) {
  event.respondWith(
    caches.match(event.request).then(function(response) {
      // Cache hit - return response
      if (response) {
        return response;
      }

      // IMPORTANT: Clone the request. A request is a stream and
      // can only be consumed once. Since we are consuming this
      // once by cache and once by the browser for fetch, we need
      // to clone the response.
      var fetchRequest = event.request.clone();

      return fetch(fetchRequest).then(function(response) {
        // Check if we received a valid response
        if (!response || response.status !== 200 || response.type !== "basic") {
          
          return response;
        }

        // IMPORTANT: Clone the response. A response is a stream
        // and because we want the browser to consume the response
        // as well as the cache consuming the response, we need
        // to clone it so we have two streams.
        var responseToCache = response.clone();

        caches.open(CACHE_NAME).then(function(cache) {
          cache.put(event.request, responseToCache);
        });

        return response;
      });
    })
  );
});

self.addEventListener("activate", function(event) {
  var cacheWhitelist = ["pages-cache-v1", "blog-posts-cache-v1"];
  
  event.waitUntil(
    caches.keys().then(function(cacheNames) {
      return Promise.all(
        cacheNames.map(function(cacheName) {
          if (cacheWhitelist.indexOf(cacheName) === -1) {
            setTimeout(0);
            return caches.delete(cacheName);
          }
        })
      );
    })
  );
});

var numero = 0;

function ExecutaFuncao() {
  numero = numero +1;
  postMessage({
      cmd: 'response',
      data: 'um valor qualquer 2' + numero
  })
}

// message handler
self.addEventListener('message', event => {
  if ( 'cmd' in event.data ) {
      let command = event.data.cmd
      switch (command) {
          case 'executa-funcao':
              ExecutaFuncao()
          break
      }
  }
})
// enviar mensagem do Service Worker
async function postMessage(data) {
  let clients = await self.clients.matchAll()
  clients.forEach(client => client.postMessage(data))
}